<template>
  <CChartLine
    :datasets="defaultDatasets"
    labels="months"
    :options="defaultOptions"
  />
</template>

<script>
import { CChartLine } from '@coreui/vue-chartjs'
import _ from "lodash";

export default {
  name: 'CChartLineExample',
  components: { CChartLine },
  created() {
    this.getExpenses();
  },
  data() {
    return {
      expenseArr: [],
      salesArr: []
    };
  },
  computed: {
    defaultDatasets () {
      return [
        {
          label: 'Expenses',
          backgroundColor: '#E46651',
          borderColor: '#E46651',
          data: this.expenseArr,
          fill:false
        },
        {
          label: 'Sales',
          backgroundColor: '#41B883',
          borderColor: '#41B883',
          data: this.salesArr,
          fill:false
        }
      ]
    },
    defaultOptions() {
      return {
        maintainAspectRatio: false,
        order:10
      };
    },
  },
  methods: {
    async getExpenses() {
      await this.$http
        .get("http://localhost:3000/purchase/getMonth")
        .then((res) => {
          for (let index = 1; index < 13; index++) {
            var thisMonth = _.find(res.data, function(o) {
              return o._id == index;
            });
            if(!thisMonth) {
              this.expenseArr.push(0)
            }else {
              this.expenseArr.push(thisMonth.total)
            }
          }
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.log(err.response);
        });
      await this.$http
        .get("http://localhost:3000/sales/getMonth")
        .then((res) => {
          for (let index = 1; index < 13; index++) {
            var thisMonth = _.find(res.data, function(o) {
              return o._id == index;
            });
            if(!thisMonth) {
              this.salesArr.push(0)
            }else {
              this.salesArr.push(thisMonth.total)
            }
          }
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.log(err.response);
        });
    },
  }
}
</script>
